// Image overlay cards

.img-overlay {
  position: relative;
  > .img-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    opacity: .25;
    object-fit: cover;
    object-position: center;
  }
  > .overlay {
    position: relative;
    z-index: 1;
  }
}

// Image backgrounds

.background-container {
  background-size: cover;
  background-position: center center;
}

.background-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

// Color overlays

.color-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

// Media Loading (Centering Blazy loading throbber)

.media--loading {
  background: #eee;
  position: static;
}

// Hide nested container gutters

.container .container, .container-fluid .container {
  &.no-nested-gutters {
    padding-left: 0;
    padding-right: 0;
  }
}
