.img-overlay {
  position: relative;
}

.img-overlay > .img-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: .25;
  object-fit: cover;
  object-position: center;
}

.img-overlay > .overlay {
  position: relative;
  z-index: 1;
}

.background-container {
  background-size: cover;
  background-position: center center;
}

.background-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.color-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.media--loading {
  background: #eee;
  position: static;
}

.container .container.no-nested-gutters, .container-fluid .container.no-nested-gutters {
  padding-left: 0;
  padding-right: 0;
}

p:last-child {
  margin-bottom: 0;
}

.breakout-container {
  left: calc(-1 * (100vw - 100%) / 2);
  width: 100vw;
  position: relative;
}

body {
  overflow-x: hidden;
}

.a2a_kit {
  white-space: nowrap;
}

.a2a_kit a {
  display: inline-block;
}

#toolbar-administration {
  z-index: 1050;
  position: relative;
}

#auth_box input[type="text"], #auth_box input[type="password"] {
  max-width: 100%;
}

svg.d324-copyright-block__developer-logo {
  fill: #11689B;
  color: #FEC114;
}

ul.slick-dots {
  position: absolute;
  bottom: 1rem;
}

/** Geysir Customization **/
.geysir-field-paragraph-wrapper .geysir-field-paragraph-wrapper .geysir-field-paragraph-links li {
  bottom: 25%;
  top: auto;
}

.geysir-field-paragraph-wrapper .geysir-field-paragraph-wrapper .geysir-field-paragraph-links li.add-before, .geysir-field-paragraph-wrapper .geysir-field-paragraph-wrapper .geysir-field-paragraph-links li.add-after {
  display: none;
}

.geysir-field-paragraph-wrapper .geysir-field-paragraph-wrapper .geysir-field-paragraph-links li .geysir-button {
  height: 1.875rem;
  width: 1.875rem;
}

.geysir-field-paragraph-links {
  display: initial !important;
  pointer-events: none;
}

.geysir-field-paragraph-links .geysir-button {
  opacity: 0;
  transform: scale(0.5);
  transition: all .35s ease;
}

.geysir-field-paragraph-wrapper:hover > .geysir-field-paragraph-links {
  pointer-events: auto;
}

.geysir-field-paragraph-wrapper:hover > .geysir-field-paragraph-links .geysir-button {
  opacity: .85;
  transform: scale(1);
}

.geysir-field-paragraph-wrapper:hover > .geysir-field-paragraph-links .geysir-button:hover {
  opacity: 1;
  transform: scale(1.1);
}

.view-blog-categories .views-row a {
  display: block;
  padding: .75rem 1rem;
  border-bottom: 1px solid #ccc;
}

.date-box {
  text-transform: uppercase;
  line-height: 1;
  height: 107px;
  width: 107px;
}

.date-box .date-month {
  font-size: 20px;
}

.date-box .date-day {
  font-size: 42px;
  font-weight: bold;
}

.event--teaser h4 {
  line-height: 1.25;
}

div.viewsreference--view-title {
  font-size: 36px;
  font-family: Constantia, "Lucida Bright", Lucidabright, "Lucida Serif", Lucida, "DejaVu Serif", "Bitstream Vera Serif", "Liberation Serif", Georgia, serif;
  margin-bottom: 2rem;
}

.paragraph > .paragraph__column {
  padding: 0;
}

.field-item__field-landing-page-sections {
  margin: 5.625rem 0;
}

.field-item__field-landing-page-sections:first-child {
  margin-top: 0;
}

.field-item__field-landing-page-sections:last-child {
  margin-bottom: 0;
}

.testimonial-card {
  background: #FFFFFF;
  padding: 2rem 2.5rem;
  margin: 5rem 10rem;
}

.testimonial-card h6 {
  line-height: 1.875;
}

.slick--view--testimonial-slider .slick-prev {
  width: 0;
  height: 0;
  border-top: 12.5px solid transparent;
  border-bottom: 12.5px solid transparent;
  border-right: 12.5px solid #111111;
  border-radius: 0;
  background: none;
  margin-left: 4rem;
}

.slick--view--testimonial-slider .slick-next {
  width: 0;
  height: 0;
  border-top: 12.5px solid transparent;
  border-bottom: 12.5px solid transparent;
  border-left: 12.5px solid #111111;
  border-radius: 0;
  background: none;
  margin-right: 4rem;
}

.slick--view--testimonial-slider .slick-prev::before, .slick--view--testimonial-slider .slick-next::before {
  content: none;
}

.testimonial .testimonial__attribution-name {
  margin: .25rem;
}

.testimonial .testimonial__attribution-title {
  margin: 0;
  line-height: 1.15;
}

.grid .testimonial-card {
  margin: 0;
  height: 100%;
}

.view-testimonial-index .grid .col article {
  height: 100%;
}

.view-testimonial-index .grid .col {
  margin-bottom: 2rem;
}

.slick--view--testimonial-slider .slick-list::before {
  content: url("/themes/d324/neal_grace/assets/images/quote-left.svg");
  position: absolute;
  left: 6rem;
  top: 2rem;
  z-index: -1;
}

.slick--view--testimonial-slider .slick-list::after {
  content: url("/themes/d324/neal_grace/assets/images/quote-right.svg");
  position: absolute;
  right: 6rem;
  bottom: 2rem;
  z-index: -1;
}

.paragraph {
  position: relative;
}

.jumbotron {
  position: relative;
  min-height: 25rem;
  text-align: center;
  background-color: #111111;
}

.jumbotron .card-img {
  position: absolute;
  object-fit: cover;
  object-position: top;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  opacity: .85;
  transition: all 0.35s ease-in-out;
}

.jumbotron:hover .card-img {
  opacity: .75;
}

.jumbotron .card-img-overlay {
  position: relative;
}

.jumbotron .paragraph--type--jumbotron h1.text-white {
  font-size: 3rem;
  padding: 0 2rem;
}

.calls-to-action a, .feature-box-ctas a {
  margin: 0 .5rem 1rem;
  padding: 1.25rem 3rem;
  font-size: 1rem;
  border: none;
}

.calls-to-action a:first-child, .feature-box-ctas a:first-child {
  margin-left: 0;
}

.calls-to-action a:last-child, .feature-box-ctas a:last-child {
  margin-right: 0;
}

@media (max-width: 991.98px) {
  .calls-to-action a, .feature-box-ctas a {
    padding: 1rem;
  }
  .feature-box .feature-box-ctas {
    margin-bottom: 0;
  }
}

.paragraph--type--separator hr {
  margin: 0;
}

.paragraph--type--separator {
  padding: 1px;
}

.adminimal-admin-toolbar.user-logged-in .paragraph--type--separator {
  opacity: 0;
  background: repeating-linear-gradient(45deg, transparent, transparent 10px, rgba(0, 0, 0, 0.025) 10px, rgba(0, 0, 0, 0.025) 20px), linear-gradient(to bottom, rgba(0, 0, 0, 0.025), rgba(0, 0, 0, 0.05));
  transition: all 0.35s ease-in-out;
}

.adminimal-admin-toolbar.user-logged-in .paragraph--type--separator:hover {
  opacity: 1;
}

.simple-block__field-media {
  float: left;
  margin-right: 1.5rem;
  margin-bottom: .5rem;
  margin-top: 0;
  max-width: 40%;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

@media (max-width: 575.98px) {
  .simple-block__field-media {
    max-width: 100%;
    width: 100%;
    margin-bottom: 2rem;
  }
}

/** Header Image Block **/
.header-image-block {
  background: #111111;
}

.header-image-block img {
  height: 400px;
  max-height: 33vh;
  min-height: 150px;
  position: relative;
  object-fit: cover;
  object-position: center top;
  width: 100%;
  opacity: .4;
}

.page-title-container h1 {
  position: absolute;
  top: 50%;
  margin-top: -20px;
  font-size: 3.5rem;
  color: #fff;
  font-weight: bold;
}
